<template>
	<div>
		<el-card>
			<h2></h2>
			<el-form :model="form" ref="form" >
				<el-form-item >
					<el-input placeholder="" v-model="form.username"   ref="username" @input="usernameOninput">
						<i slot="prefix" class="el-input__icon  el-icon-user-solid"></i>
					</el-input>
					<div class="flagError">
						<span v-show="flagUsername" > </span>
					</div>
				</el-form-item>
				<el-form-item >
					<el-input placeholder="" v-model="form.password" show-password ref="password"  @focus="passwordOnfocus"  @input="passwordOninput">
						<i slot="prefix" class="el-input__icon el-icon-s-opportunity"></i>
					</el-input>
					<div class="flagError">
						<span v-show="flagPassword" > </span>
					</div>
					<router-link to="/repassword"></router-link>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="userLogin(form)" ></el-button>
					<el-button @click="userRegister"></el-button>
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				form:{
					username:'',
					password:''
				},
				flagPassword:false,
				flagUsername:false
			}
		},
		methods:{
			//
			usernameOninput(){
				this.flagUsername=false;
			},
			//
			passwordOnfocus(){
				if(this.form.username==''){
					this.flagUsername=true;
					this.$refs.username.focus();
				}

			},
			//
			passwordOninput(){
				this.flagPassword=false;
			},
			//
			userLogin:function(form){
				//
				if(this.form.password==''){
					this.flagPassword=true;
					this.$refs.password.focus();
				}
				//
				if(this.flagUsername||this.flagPassword){
					return false;
				}
				this.axios.post('api/users/login',{username:this.form.username,password:this.form.password}).then(response=>{
					if(response.data.status==1){
						this.mes.errorMessage(response.data.message);
						this.form.username='';
						this.form.password='';
					}
					else{
						localStorage.setItem('token',response.data.data.token);
						localStorage.setItem('username',response.data.data.user[0].username);
						localStorage.setItem('_id',response.data.data.user[0]._id);
						this.mes.successMessage(',');
						setTimeout(()=>{
							this.$router.push({path:'/'});
						},2000);
					}
				});
				
			},
			//
			userRegister(){
				this.$router.push({path:'/registerPage'});
			}
		}


	}
</script>
<style lang="scss" scoped>
	@import'../style/base/_base.scss';
	.el-card{
		@include base_el_card;
		h2{
			margin-bottom:20px;
		}
		.flagError{
			@include base_error_message;
		}
		a{
			float:right;
			font-size:10px;
			color:black;
			text-decoration:none;
			&:hover{
				color:red;
			}
		}
	}
	
</style>


