<template>
	<div>
		<el-card>
			<h2></h2>
			<el-form :model="form">
				<el-form-item>
					<el-input placeholder="" v-model="form.username" ref="username" @input="usernameOnInput" @blur="usernameOnBlur">
						<i slot="prefix" class="el-input__icon  el-icon-user-solid"></i>
					</el-input>
					<div class="flagError">
						<span v-if="!flagUsername" class="flag_black">{{usernameMessage}}</span>
						<span v-else>{{usernameMessage}}</span>
					</div>
				</el-form-item>
				<el-form-item>
					<el-input placeholder="" v-model="form.password" show-password ref="password" @focus="passwordOnFocus"  @input="passwordOnInput">
						<i slot="prefix" class="el-input__icon  el-icon-more"></i>
					</el-input>
					<div class="flagError">
						<span v-if="!flagPassword" class="flag_black">{{passwordMessage}}</span>
						<span v-else>{{passwordMessage}}</span>
					</div>
				</el-form-item>
				<el-form-item>
					<el-input placeholder="" v-model="form.repassword" show-password  ref="repassword" @focus="repasswordOnFocus" @input="repasswordOnInput">
						<i slot="prefix" class="el-input__icon  el-icon-more"></i>
					</el-input>
					<div class="flagError">
						<span v-if="!flagRepassword" class="flag_black">{{repasswordMessage}}</span>
						<span  v-else>{{repasswordMessage}}</span>

					</div>
				</el-form-item>
				<el-form-item>
					<el-input placeholder="" v-model="form.userMail" ref="userMail" @focus="userMailOnFocus" @input="userMailOnInput" >
						<i slot="prefix" class="el-input__icon  el-icon-message"></i>
					</el-input>
					<div class="flagError">
						<span v-if="flagUserMail" > {{userMailMessage}}</span>
					</div>
				</el-form-item>
				<el-form-item>
					<el-input placeholder="" v-model="form.userPhone" @focus="userPhoneOnFocus" @input="userPhoneOnInput" ref="userPhone">
						<i slot="prefix" class="el-input__icon  el-icon-mobile-phone"></i>
					</el-input>
					<div class="flagError">
						<span v-if="flagUserPhone" > {{userPhoneMessage}}</span>
					</div>
				</el-form-item>				
				<el-form-item>
					<el-button @click="userRegister" type="primary"></el-button>
				
					<el-button @click="userLogin"></el-button>
			
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				form:{
					username:'',
					password:'',
					repassword:'',
					userMail:'',
					userPhone:''
				},
				flagUsername:false,
				usernameMessage:'  ',
				flagPassword:false,
				passwordMessage:'  ',
				flagRepassword:false,
				repasswordMessage:'  ',
				flagUserPhone:false,
				userPhoneMessage:'',
				flagUserMail:false,
				userMailMessage:'',	
			}
		},
		methods:{
			//
			usernameOnInput(){
				this.flagUsername=false;
				this.usernameMessage=' ';
			},
			//
			usernameOnBlur(){
				this.axios.post('api/users/userDetail',{username:this.form.username}).then(response=>{
					if(response.data.status==0){
						this.form.username='';
						this.usernameMessage="";
						this.flagUsername=true;
						return this.$refs.username.focus();
					}
				});
			},
			//
			passwordOnFocus(){
				let exp=/^[a-zA-z_]{1}[\w]{5,}$/;
				if(this.form.username==''){
					this.flagUsername=true;
					this.usernameMessage='';
					return this.$refs.username.focus();
				}
				if(!exp.test(this.form.username)){
					this.flagUsername=true;
					this.usernameMessage='';
					return this.$refs.username.focus();
				}
			},
			//;
			passwordOnInput(){
				this.flagPassword=false;
				this.passwordMessage='';
			},
			//
			repasswordOnFocus(){
				let exp=/^[a-zA-z_]{1}[\w]{5,}$/;
				if(this.form.password==''){
					this.flagPassword=true;
					this.passwordMessage='';
					return this.$refs.password.focus();
				}
				if(!exp.test(this.form.password)){
					this.flagPassword=true;
					this.passwordMessage='';
					return this.$refs.password.focus();
				}
			},
			//;
			repasswordOnInput(){
				this.flagRepassword=false;
				this.repasswordMessage=''
			},
			//
			userMailOnFocus(){
				if(this.form.repassword==''){
					this.flagRepassword=true;
					this.repasswordMessage='';
					return this.$refs.repassword.focus();
				}
				if(this.form.password!==this.form.repassword){
					this.flagRepassword=true;
					this.repasswordMessage='';
					return this.$refs.password.focus();
				}
			},
			//
			userMailOnInput(){
				this.flagUserMail=false;
			},
			//
			userPhoneOnFocus(){
				let exp=/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
				if(this.form.userMail==''){
					this.flagUserMail=true;
					this.userMailMessage='';
					return this.$refs.userMail.focus();
				}
				if(!exp.test(this.form.userMail)){
					this.flagUserMail=true;
					this.userMailMessage='';
					return this.$refs.userMail.focus();
				}
			},
			//
			userPhoneOnInput(){
				this.flagUserPhone=false;
			},
			//
			userLogin(){
				this.$router.push({path:'/loginPage'})
			},

			//
			userRegister(){
				//
				let exp=/^(13[0-9]|14[5-9]|15[012356789]|166|17[0-8]|18[0-9]|19[8-9])[0-9]{8}$/;
				if(this.form.userPhone==''){
					this.flagUserPhone=true;
					this.userPhoneMessage='';
					return this.$refs.userPhone.focus();
				}
				//
				if(!exp.test(this.form.userPhone)){
					this.flagUserPhone=true;
					this.userPhoneMessage='';
					return this.$refs.userPhone.focus();
				}
				//
				if(this.flagUsername||this.flagPassword||this.flagRepassword||this.flagUserMail||this.flagUserPhone){
					return false;
				}
				//api
				this.axios.post('api/users/register',this.form).then(response=>{
					if(response.data.status==1){
						this.errorMessage(response.data.message);
					}
					else{
						//
						this.login();
					}
				});
			},
			//
			login(){
				this.axios.post('api/users/login',{username:this.form.username,password:this.form.password}).then(response=>{
					if(response.data.status==1){
						this.mes.errorMessage(response.data.message);
					}
					else{
						localStorage.setItem('token',response.data.data.token);
						localStorage.setItem('username',response.data.data.user[0].username);
						localStorage.setItem('_id',response.data.data.user[0]._id);
						this.mes.successMessage(',');
						setTimeout(()=>{
							this.$router.push({path:'/'});
						},2000);
					}
				});
			}

		}
	}
</script>
<style lang="scss" scoped>
@import '../style/base/_base.scss';
	.el-card{
		@include base_el_card;
		h2{
			margin-bottom:20px;
		}
		.flagError{
			@include base_error_message;
			.flag_black {
				color:#333;
			}
		}

		
	}
	
</style>


