<template>
<div class="main">
	<div class="user">
		<div class="userImg">
			<img :src="user.userImg">
		</div>
		
		<div class="userInfo">
			<p><span>:</span>{{user.username}}</p>
			<p><span>:</span>{{user.userMail}}</p>
			<p><span>:</span>{{user.userPhone}}</p>
			<p><span>:</span>{{user.userDescription}}</p>
		</div>
		
	</div>
	<div class="website">
		<h3>:</h3>
		<ul>
			<li>
	              1.vue-clivueaxiosroutervuexelement-uiexpressmultermongoDBapiapi
            </li>
			<li>
                  2.
            </li>
            <li>   	
                  3.
            </li>
            <li>
                  4.star  
            </li>
		</ul>
		
	</div>
</div>
</template>
<script>
	export default{
		name:'index',
		data(){
			return{
				user:''
			}
		},
		created(){
			this.getData();	
		},
		methods:{
			getData(){
				this.axios.post('api/users/userDetail',{user_id:localStorage._id}).then(response=>{
					if(response.data.status==0){
						this.user=response.data.data[0];
					}
					else{
						this.mes.errorMessage(response.data.message);

					}
				})
			}
		
		}

	}
</script>
<style scoped lang="scss">
.main{
	display:flex;
	flex-direction:column;
	padding:20px 10px;
	.user{
		height:150px;
		display:flex;
		.userImg{
			width:200px;
			display:flex;
			justify-content:center;
			align-items:center;
			img{
				width:100px;
				border-radius:50%;
			}
		}
		.userInfo{
			flex:1;
			display:flex;
			flex-direction:column;
			justify-content:center;
			p{
				font-size:14px;
				span{
					display:inline-block;
					width:60px;
					text-align:right;
					margin-right:10px;
					line-height:26px;
				}
			}

		}

	}
	.website{
		flex:1;
		background:#eef1f6;
		padding:30px 15px 10px 15px;
		h3{
			line-height:26px;
			height:26px;
		}
		ul{
			margin-left:15px;
			list-style:none;
			li{

				margin-top:8px;
				line-height:24px;
				font-size:16px;

			}
		}
	}
}

</style>


