<template>
<div>
	<el-card>
		<h2></h2>
		<!--  -->
		<el-form v-if="flagshowUserInfo" :model="form1">
			<el-form-item>
				<el-input placeholder="" v-model="form1.username" ref="username" @input="usernameOnInput">
					<i slot="prefix" class="el-input__icon  el-icon-user-solid"></i>
				</el-input>
				<div class="flagError">
					<span v-show="flagUsername" > </span>
				</div>
			</el-form-item>
			<el-form-item>
				<el-input placeholder="" v-model="form1.userMail" @focus="userMailOnFocus"  ref="userMail" @input="userMailOnInput">
					<i slot="prefix" class="el-input__icon  el-icon-message"></i>
				</el-input>
				<div class="flagError">
					<span v-show="flagUserMail" > </span>
				</div>
			</el-form-item>
			<el-form-item>
				<el-input placeholder="" v-model="form1.userPhone" ref="userPhone" @focus="userPhoneOnFocus" @input="userPhoneOnInput">
					<i slot="prefix" class="el-input__icon  el-icon-mobile-phone"></i>
				</el-input>
				<div class="flagError">
					<span v-show="flagUserPhone" > </span>
				</div>
			</el-form-item>
			<el-form-item>
				<el-button @click="checkUser"  type="primary"></el-button>
				<el-button @click="onRegister"></el-button>
				<el-button @click="onLogin"></el-button>
			</el-form-item>
		</el-form>
		<!--  -->
		<el-form v-else :model="form2">
			<el-form-item>
				<el-input placeholder="" v-model="form2.repassword"  ref="repassword" @blur="repasswordOnBlur" @input="repasswordOnInput" ></el-input>
				<div class="flagError">
					<span v-show="flagRepasswrd">{{repasswordMessage}}</span>
				</div>
			</el-form-item>
			<el-form-item>
				<el-button @click="changeUserPassword"  type="primary"></el-button>
			</el-form-item>
		</el-form>
	</el-card>
</div>
</template>
<script>
	export default{
		data(){
			return{
				form1:{
					userMail:'',
					userPhone:'',
					username:''
				},
				form2:{
					repassword:'',
				    password:''
				},
				repasswordMessage:'',
				flagUsername:false,
				flagUserMail:false,
				flagUserPhone:false,
				flagRepasswrd:false,

				flagshowUserInfo:true		
			}
		},
		methods:{
			//
			usernameOnInput(){
				this.flagUsername=false;
			},
			//
			userMailOnFocus(){
				if(this.form1.username==''){
					this.flagUsername=true;
					this.$refs.username.focus();
				}
			},
			//
			userMailOnInput(){
				this.flagUserMail=false;
			},
			//
			userPhoneOnFocus(){
				if(this.form1.userMail==''){
					this.flagUserMail=true;
					this.$refs.userMail.focus();
				}
			},
			//
			userPhoneOnInput(){
				this.flagUserPhone=false;
			},
			//
			onRegister(){
				this.$router.push({path:'/registerPage'});
			},
			//
			onLogin(){
				this.$router.push({path:'/loginPage'});
			},
			//
			checkUser(){
				//
				if(this.form1.userPhone==''){
					this.flagUserPhone=true;
					this.$refs.userPhone.focus();
				}
				//
				if(this.flagUsername||this.flagUserMail||this.flagUserPhone){
					return false;
				}
				//
				this.axios.post('api/users/findPassword',this.form1).then(response=>{
					if(response.data.status==1){
						this.mes.errorMessage(response.data.message);
						this.form1.userMail='';
						this.form1.userPhone='';
						this.form1.username='';
					}
					else{
						 this.form2.password=response.data.data[0].password;
						 this.flagshowUserInfo=false;
					}
				});
			},
			//,
			repasswordOnBlur(){
				let exp=/^[a-zA-z_]{1}[\w]{5,}$/;
				if(this.form2.repassword==this.form2.password){
					this.$refs.repassword.focus();
					this.repasswordMessage='';
					this.flagRepasswrd=true;
				}
				if(!exp.test(this.form2.repassword)){
					this.flagRepasswrd=true;
					this.repasswordMessage='';
					return this.$refs.repassword.focus();
				}

			},
			//
			repasswordOnInput(){
				this.flagRepasswrd=false;
			},
			//
			changeUserPassword(){
				//
				 if(this.form2.repassword==''){
				 	this.repasswordMessage=""
				 	this.flagRepasswrd=true;
				 	return this.$refs.repassword.focus();
				 }
				 // 
				 if(this.flagRepasswrd){
				 	return false;
				 }

				let sendDate={...this.form1,repassword:this.form2.repassword};
				//
				this.axios.post('api/users/findPassword',sendDate).then(response=>{
					if(response.data.status==1){
						 this.mes.errorMessage(response.data.message);
					}
					else{
						this.mes.successMessage(',');
						setTimeout(()=>{this.$router.push({path:'/'})},1000);
					}
					
				});
			}

		}
	}
</script>
<style lang="scss" scoped>
	@import '../style/base/_base.scss';
	.el-card{
		@include base_el_card;
		h2{
			margin-bottom:20px;
		}
		.flagError{
			@include base_error_message;
		}
	}
	
</style>


